.addModal .title {
  margin: 0 auto;
  text-align: center;
  font-size: 18px;
}
.addModal .input {
  width: 100%;
  height: 40px;
  display: block;
  border: 0;
  border-bottom: 1px solid #eee;
  margin-top: 80px;
  outline: none;
}
.addModal .input:focus {
  border-color: #f00;
}
.content {
  display: flex;
}
.content .tool {
  flex: 1;
  padding-left: 20px;
}
.roleList {
  border-right: 1px solid #eee;
  width: 300px;
  padding-right: 20px;
}
.menuTree {
  margin-top: 20px;
}
.menuTree .title {
  font-size: 14px;
  font-weight: normal;
}
.identityList {
  display: flex;
  margin-bottom: 30px;
  margin-top: 10px;
}
.identityList .item {
  font-size: 12px;
  margin-right: 15px;
}
